import * as React from 'react';
import { NavLink, Redirect } from 'react-router-dom';
import { NoteEditor } from '../../components/NoteEditor';
import { useData } from '../../model';
import { ToolSection } from '../../framework/ToolSection';
import { useQueryState } from '../../lib/UseQueryState';
import * as styles from '../../layout/styles';
import { cx } from 'emotion';

export const EditNotePage: React.FC<{id?: string }> = props => {
    const {data, refreshData} = useData();
    let tags: {[key: string]: number} = {};
    const [goBack, setGoBack] = React.useState(false);
    const [source] = useQueryState(null, 'source');

    if (goBack) {
        return <Redirect to={source ? decodeURIComponent(source) : '/'} />;
    }

    for (const note of data.notes || []) {

        if(!note.tags || !note.tags.length) {
            continue;
        }

        for(const tag of note.tags.split(' ')) {
            if(!tags[tag]) {
                tags[tag] = 0;
            }

            tags[tag] += 1;
        }
    }

    const availableTags = Object.keys(tags).sort((a, b) => tags[b] - tags[a]);

    if (props.id) {
        let note = data.notes.find(x => x.id === props.id);

        if (!note) {
            return <div>Note not found.</div>
        }

        return (
            <>
                <div style={{height: '100%', display: 'flex', flexDirection: 'column'}}>
                    <ToolSection>
                        <div className={cx(styles.horizontalSpace('.5rem'))}>
                            <NavLink to={source ? decodeURIComponent(source) : '/'} className="button">Back</NavLink>
                            {!note.noteFor && <NavLink to={`/notes/${note.id}`} className="button">Show</NavLink>}
                        </div>
                    </ToolSection>
                    <NoteEditor {...note} data={data} text={note.content}  availableTags={availableTags} noteMode={note.mode||'tree'} dailyDate={note.dailyDate} onNoteAdded={() => { refreshData(); setGoBack(true); }} />
                </div>
            </>
        );
    }

    const urlParams = new URLSearchParams(window.location.search);
    const title = urlParams.get('title');
    const contextId = urlParams.get('context');

    return (
        <>
            <div style={{height: '100%', display: 'flex', flexDirection: 'column'}} >
                <ToolSection>
                    <NavLink to={`/`} className="button">Back</NavLink>
                </ToolSection>
                <NoteEditor title={title || undefined} contextId={contextId} availableTags={availableTags} data={data} noteFor={null} dailyDate={null} noteMode={'tree'} onNoteAdded={() => { refreshData(); setGoBack(true); }} />
            </div>
        </>
    );
}