import * as React from 'react';
import { ContextBar } from '../../components/ContextBar';
import { useQueryState } from '../../lib/UseQueryState';
import { ToolSection } from '../../framework/ToolSection';
import { useData } from '../../model';
import moment from 'moment';
import { getNoteTitle, Note } from '../../db/note';
import { ContextTag } from '../../components/ContextTag';
import { TagList } from '../../components/TagInput';
import { MarkedViewer } from '../../components/MarkedViewer';
import _ from 'lodash';
import * as ContextActions from '../../lib/contextActions';
import { TreeViewer } from '../../components/TreeViewer';

export const DailyNotePage: React.SFC<{ }> = (props) => {
    const [contextId, setContextId] = useQueryState(null, 'context');
    const {data} = useData();

    const oneWeekAgo = moment().add(-7,'d');
    
    const filteredContexts = ContextActions.getFilteredContextIds(contextId, data);
    const dailyNotes = _.orderBy(data.notes.filter(x => x.dailyDate && moment(x.dailyDate) > oneWeekAgo && (!contextId || filteredContexts.indexOf(x.contextId!) !== -1)), n => n.dailyDate,  'desc');

    function renderViewer(note: Note) {
        if (!note.mode || note.mode === 'markdown') {
            return <MarkedViewer markdown={note.content} contextId={note.contextId} />;
        } 
          
        return <TreeViewer json={note.content} contextId={note.contextId} />;
    }

    return (
        <div style={{display: 'flex', height: '100%'}}>
            <ContextBar contextId={contextId} onChange={setContextId} />
            <div style={{width: '100%'}}>
                <ToolSection>
                </ToolSection>
                <div className="panel">
                    {dailyNotes.map(n => (
                        <div key={n.id} className="content" style={{padding: '1rem'}}>
                            <h1>{getNoteTitle(n)}</h1>
                            <div style={{display: 'flex', justifyContent: 'space-between'}}>
                                <TagList tags={n.tags} />
                                <ContextTag contextId={n.contextId} />
                            </div>
                            {renderViewer(n)}
                        </div>))}
                </div>
            </div>
        </div>
    )
};
