import * as React from 'react';
import * as draftStorage from '../draftStorage';
import { TagInput, TagState } from './TagInput';
import { ToolSection } from '../framework/ToolSection';
import { SimpleSplitEditor, SplitMode, SplitModeSelector, NoteModeSelector } from './SimpleSplitEditor';
import { isMobile } from './Layout';
import { db } from '../db/db';
import { Reference, NoteMode, getNoteTitle } from '../db/note';
import { TreeEditor } from './TreeEditor';
import { SettingsCompactor } from './SettingsCompactor';
import './NoteEditor.scss';
import { Loader } from '../components/Loader';
import { ContextSelector } from '../components/ContextSelector';
import { Data } from '../model';
import moment from 'moment';

export class NoteEditor extends React.Component<{onNoteAdded: () => void, id?: string, title?: string, text?: string, tags?: string, contextId: string|null, availableTags: string[], noteFor: Reference|null, noteMode?: NoteMode, data: Data, dailyDate: string|null}, {title: string, text: string, tags: string, tagState: TagState, splitMode: SplitMode, noteMode: NoteMode, contextId: string|null, loaded: boolean, dailyDate: string|null}> {
    constructor(props: any) {
        super(props);

        this.state = {
            loaded: false,
            title: '', 
            text: '', 
            tags: '', 
            tagState: new TagState([], []), 
            splitMode: isMobile() ? SplitMode.Editor : SplitMode.Split, 
            noteMode: 'tree',
            contextId: null,
            dailyDate: null,
        };

        this.onTextChanged = this.onTextChanged.bind(this);
        this.onSave = this.onSave.bind(this);
    }

    async componentDidMount() {
        const draft = await draftStorage.getDraft(this.props.id);

        const title = this.props.title;
        let text = this.props.text;
        const tags = this.props.tags;
        const noteMode = this.props.noteMode;
        const contextId = this.props.contextId;
        const dailyDate = this.props.dailyDate;

        let state: any = {};

        if (draft !== null && draft !== text && draft.length > 0) {
            text = draft;
        }

        if (title !== undefined) {
            state.title = title;
        }
        
        if (text !== undefined) {
            state.text = text;
        }

        if (noteMode !== undefined) {
            state.noteMode = noteMode;
        }

        if (contextId !== undefined) {
            state.contextId = contextId;
        }

        if (dailyDate !== undefined) {
            state.dailyDate = dailyDate;
        }

        state.tagState = this.state.tagState
            .withAvailableTags(this.props.availableTags);

        if (tags && tags.length > 0) {
            state.tagState = state.tagState.withSelectedTags(tags.split(' '));
        }

        state.loaded = true;
        this.setState(state);
    }

    componentWillReceiveProps(props: any) {
        this.setState({tagState: this.state.tagState.withAvailableTags(props.availableTags)});
    }

    onTitleChanged(e: React.ChangeEvent<HTMLInputElement>) {
        this.setState({ title: e.target.value });
    }

    onTextChanged(text: string) {
        if (text !== this.props.text) {
            draftStorage.setDraft(this.props.id, text);
        }

        this.setState({text})
    }

    async onSave() {
        const id = this.props.id || null;

        if (id) {
            let note = await db.notes.get(id);

            if (note === null) {
                throw new Error('Note is null?');
            }

            note.title = this.state.title;
            note.content = this.state.text;
            note.tags = this.state.tagState.getTags();
            note.mode = this.state.noteMode;
            note.contextId = this.state.contextId;
            note.dailyDate = this.state.dailyDate;

            await db.notes.save(note);
        } else {
            await db.notes.create({
                title: this.state.title,
                content: this.state.text,
                tags: this.state.tagState.getTags(),
                noteFor: null,
                mode: this.state.noteMode,
                contextId: this.state.contextId,
                dailyDate: this.state.dailyDate
            });
        }

        await draftStorage.clearDraft(this.props.id);
        this.props.onNoteAdded();
    }

    render() {
        const updateDailyDate = (value: string) => {
            if (value === '') {
                this.setState({dailyDate: null});
            } else {
                this.setState({dailyDate: new Date(value).toISOString()});
            }
        }

        if (!this.state.loaded) {
            return <Loader />;
        }

        return (
            <div style={{width: '100%', flex: 1, display: 'flex', flexDirection: 'column'}}>
                <ToolSection verticalAround={false}>
                    <SettingsCompactor wideStyle={{justifyContent: 'space-between'}} alwaysVisible={() => (
                        <>
                            {this.props.id === undefined ?
                                <button className="button is-primary" onClick={this.onSave}>Add note</button> :
                                <button className="button is-primary" onClick={this.onSave}>Update note</button>}
                            <ContextSelector contextId={this.state.contextId} data={this.props.data} onChange={contextId => this.setState({ contextId })} />
                            <input type="date" className="input is-large" value={(this.state.dailyDate && moment(this.state.dailyDate).format('YYYY-MM-DD')) || ''} onChange={e => updateDailyDate(e.target.value)} />
                        </>
                    )}>
                        <NoteModeSelector mode={this.state.noteMode} onModeChange={noteMode => this.setState({ noteMode })} />
                        <SplitModeSelector mode={this.state.splitMode} onModeChange={splitMode => this.setState({ splitMode })} />
                    </SettingsCompactor>
                </ToolSection>
                <ToolSection>
                    {!this.props.noteFor && !this.state.dailyDate && (
                        <>
                            <input type="text" placeholder="Title" className="input is-large is-wide" value={this.state.title} onChange={this.onTitleChanged.bind(this)} />
                            <TagInput tagState={this.state.tagState} onChange={s => this.setState({tagState: s})} />
                        </>
                    )}
                    {this.state.dailyDate && (
                        <h1>{getNoteTitle({ dailyDate: this.state.dailyDate, title: this.state.title, content: this.state.text })}</h1>
                    )}
                </ToolSection>
                <div style={{flex: 1}}>
                    {this.state.noteMode === 'markdown' ? (
                        <SimpleSplitEditor text={this.state.text} expand={true} onTextChanged={this.onTextChanged} mode={this.state.splitMode} data={this.props.data} contextId={this.state.contextId} />
                    ) : (
                        <TreeEditor text={this.state.text} onTextChanged={this.onTextChanged} contextId={this.state.contextId} />
                    )}
                </div>
            </div>
        );
    }
}