import {createInstance} from 'localforage';

const draftStore = createInstance({ name: 'drafts' });

function getDraftKey(id: string|undefined) {
    if(!id) {
        return 'draft-new';
    }

    return `draft-${id}`;
}

export async function hasDraft(id: string|undefined) {
    const draft = await draftStore.getItem<string>(getDraftKey(id));
    return !!draft;
}

export async function getDraftIds() {
    const keys = await draftStore.keys();
    return keys.map(k => k.replace('draft-', ''));
}

export async function getDraft(id: string|undefined) {
    return await draftStore.getItem<string>(getDraftKey(id));
}

export async function setDraft(id: string|undefined, text: string) {
    return await draftStore.setItem(getDraftKey(id), text);
}

export async function clearDraft(id: string|undefined) {
    return await draftStore.removeItem(getDraftKey(id));
}